import { Base64 } from 'js-base64';
import pako from 'pako';

// ่งฃๅ
export function unzip(b64Data) {
  let strData = Base64.atob(b64Data);
  let charData = strData.split('').map(function (x) {
    return x.charCodeAt(0);
  });
  let binData = new Uint8Array(charData);
  let data = pako.ungzip(binData);
  // โๅ็ๅค็ๆฐๆฎ๏ผ้ฒๆญขๅๅญๆบขๅบๆฅ้โ
  let str = '';
  const chunk = 8 * 1024;
  let i;
  for (i = 0; i < data.length / chunk; i++) {
    str += String.fromCharCode.apply(null, data.slice(i * chunk, (i + 1) * chunk));
  }
  str += String.fromCharCode.apply(null, data.slice(i * chunk));
  // โๅ็ๅค็ๆฐๆฎ๏ผ้ฒๆญขๅๅญๆบขๅบๆฅ้โ
  const unzipStr = Base64.decode(str);
  let result = '';
  // ๅฏน่ฑกๆๆฐ็ป่ฟ่กJSON่ฝฌๆข
  try {
    result = JSON.parse(unzipStr);
  } catch (error) {
    if (/Unexpected token o in JSON at position 0/.test(error)) {
      // ๅฆๆๆฒกๆ่ฝฌๆขๆๅ๏ผไปฃ่กจๅผไธบๅบๆฌๆฐๆฎ๏ผ็ดๆฅ่ตๅผ
      result = unzipStr;
    }
  }
  return result;
}
